<template>
  <el-row class="footer">
    <el-col :span="20" :offset="2">
      <p>Copyright1999-{{Nyear}}  .All Rights Reserved</p>
      <p>ICP10218183 ICP161188 11010802020593 130052</p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: 'Footer',
  data: function () {
    return {
      Nyear: '2018',
    }
  },
  create:function(){
    const Ndate = new Date();
    this.Nyear = Ndate.getFullYear();
  }
}
</script>

<style scoped>
.footer{ width: 100%; height: auto; padding: 20px 0px; line-height: 30px; background: rgba(255,255,255,1); font-size: 14px; color: #333; text-align: center; position: relative; bottom: 0; box-sizing: border-box; display: block; }
</style>



